\n                            <van-icon name=\"arrow-down\"  style=\"font-size:20px;\"/>\r\n                        </p>\r\n                        <p class=\"p2\">所有时间<van-icon name=\"arrow-down\" style=\"font-size:20px;\"/></p>\r\n                        <ul class=\"u1\" >\r\n                            <li @click=\"storeChange(0,'所有门店')\">所有门店</li>\r\n                            <li @click=\"storeChange(L.id,L.name)\" v-for=\"L in list\" :key=\"L.id\">{{L.name}}</li>\r\n                            &lt;!&ndash; <li @click=\"off(1)\">{{name}}</li>\r\n                            <li @click=\"off(17)\">{{name1}}</li>\r\n                            <li @click=\"off(18)\">{{name2}}</li> &ndash;&gt;\r\n                        </ul>\r\n                    </van-popup>\r\n            </div>\r\n            <p class=\"p2\" @click=\"AllTime()\" >\r\n              {{store2}}\r\n                <van-icon name=\"arrow-down\" style=\"font-size:20px;\"/>\r\n            </p>\r\n            <div class=\"D2\" ref=\"D2\">\r\n                <van-popup v-model=\"show\" position=\"top\" :click-overlay=\"false\">\r\n                    <p class=\"p1\" >{{store1}}\r\n                        <van-icon name=\"arrow-down\"  style=\"font-size:20px;\"/>\r\n                    </p>\r\n                    <p class=\"p2\">所有时间<van-icon name=\"arrow-down\" style=\"font-size:20px;\"/></p>\r\n                    <ul class=\"u2\">\r\n                        <li @click=\"timeChange1()\">所有时间</li>\r\n                        <li @click=\"timeChange2()\">当天</li>\r\n                        <li @click=\"timeChange3()\">近七天</li>\r\n                        <li>自定义：</li>\r\n                        <input type=\"text\" :value=\"time1\" @focus=\"in1()\"  readonly=\"readonly\" />&nbsp;—\r\n                        <input type=\"text\" :value=\"time2\" @focus=\"in2()\"  readonly=\"readonly\" />\r\n                        <button @click=\"timeChange()\">确定</button>\r\n                    </ul>\r\n                </van-popup>\r\n                <van-popup type=\"date\" v-model=\"show2\" position=\"bottom\"  :click-overlay=\"false\" style=\"height:45%;\">\r\n                    <van-datetime-picker\r\n                    v-model=\"currentDate\"\r\n                    type=\"date\"\r\n                    @confirm=\"Finish\"\r\n                    @cancel=\"Finish2\"\r\n                    :max-date=\"date\"\r\n                    />\r\n                </van-popup>\r\n                 <van-popup type=\"date\" v-model=\"show3\" position=\"bottom\" :click-overlay=\"false\" style=\"height:45%;\">\r\n                    <van-datetime-picker\r\n                    v-model=\"currentDate\"\r\n                    type=\"date\"\r\n                    @confirm=\"Finish1\"\r\n                    @cancel=\"Finish3\"\r\n                    :max-date=\"date\"\r\n                    />\r\n                </van-popup>\r\n             </div>\r\n        </div>-->\r\n        <div class=\"main-top\">\r\n            <div><p class=\"sp1\"><b>{{Order}}</b></p>预约(个)</div>\r\n            <div><p class=\"sp1\"><b>{{sum_number}}</b></p>订单(个)</div>\r\n            <div><p class=\"sp1\"><b>{{totel_price}}</b></p>订单金额(元)</div>\r\n        </div>\r\n        <div class=\"main-m\">\r\n            <div><p class=\"sp1\"><b>{{consumeCustomerNum}}</b></p>会员卡(个)</div>\r\n            <div><p class=\"sp1\"><b>{{sumCharge}}</b></p>充卡金额(元)</div>\r\n            <div><p class=\"sp1\"><b>{{sumBalance}}</b></p>开卡余额(元)</div>\r\n        </div>\r\n        <div class=\"main-bon\">\r\n            <div><p class=\"sp1\"><b>{{customerBalanceNum}}</b></p>新增客户(个)</div>\r\n            <div><p class=\"sp1\"><b>{{customerNum}}</b></p>新增用户(个)</div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\n\texport default {\r\n\t\tdata() {\r\n\t\t\treturn {\r\n                date:new Date(),\r\n                usr:'',\r\n                pas:'',\r\n                store1:\"所有门店\",\r\n                store2:\"所有时间\",\r\n                // store:\"我是门店\",\r\n                // store3:\"我是时间\",\r\n                show:false,\r\n                show1:false,\r\n                show2:false,\r\n                show3:false,\r\n                currentDate: new Date(),\r\n                time1:'',\r\n                time2:'',\r\n                Order:\"\",\r\n                sum_number:\"\",\r\n                totel_price:'',\r\n                consumeCustomerNum:'',\r\n                sumCharge:'',\r\n                sumBalance:'',\r\n                customerBalanceNum:'',\r\n                customerNum:'',\r\n                list:''\r\n                // name:'',\r\n                // name1:'',\r\n                // name2:'',\r\n                // name3:'',\r\n                // id:'',\r\n                // id1:'',\r\n                // id2:'',\r\n                // id3:''\r\n\t\t\t}\r\n\t\t},\r\n\t\tcreated(){\r\n            this.GOIN()\r\n        },\r\n\t\tmethods: {\r\n            //预约请求返回数据\r\n            GOIN(){\r\n                //获取店铺信息\r\n\r\n                this.$axios({\r\n\t\t\t\t\turl: this.$BASE_STORE_URL +  \"shop/queryShopForAll\",\r\n                    method: \"get\",\r\n\t\t\t\t}).then(res => {\r\n                    console.log(res)\r\n                    this.list = res.data.data\r\n                    // this.name = res.data.data[0].name\r\n                    // this.name1 = res.data.data[1].name\r\n                    // this.name2 = res.data.data[2].name\r\n                    // this.name3 = res.data.data[3].name\r\n\r\n                    // this.id = res.data.data[0].id\r\n                    // this.id1 = res.data.data[1].id\r\n                    // this.id2 = res.data.data[2].id\r\n                    // this.id3 = res.data.data[3].id\r\n\r\n                })\r\n                //订单\r\n                this.$axios({\r\n                    url: this.$BASE_ORDER_URL + \"ordermanage/queryOrderSummary\",\r\n                    method:\"get\",\r\n                    params:{\r\n                        departmentId:null,\r\n                        startTime:this.time1==\"\"?null:this.time1,\r\n                        endTime:this.time2==\"\"?null:this.time2,\r\n                    }\r\n                    }).then(res => {\r\n                            console.log(res)\r\n                            this.sum_number = res.data.data.sumNumber\r\n                            this.totel_price = res.data.data.totelPrice\r\n                        })\r\n                //预约\r\n                this.$axios({\r\n\t\t\t\t\turl: this.$BASE_ORDER_URL +  \"schedule/queryScheduleSummary\",\r\n                    method: \"get\",\r\n                    params:{\r\n                        departmentId:null,\r\n                        startTime:this.time1==\"\"?null:this.time1,\r\n                        endTime:this.time2==\"\"?null:this.time2,\r\n                    }\r\n\t\t\t\t}).then(res => {\r\n                    console.log(res)\r\n                    this.Order = res.data.data\r\n                })\r\n                //会员\r\n                this.$axios({\r\n\t\t\t\t\turl: this.$BASE_USER_URL +  \"gogirl_user/countCustomerInfo\",\r\n                    method: \"get\",\r\n                    params:{\r\n                        departmentId:null,\r\n                        startTime:this.time1==\"\"?null:this.time1,\r\n                        endTime:this.time2==\"\"?null:this.time2,\r\n                    }\r\n\t\t\t\t}).then(res => {\r\n                    console.log(res)\r\n                    this.consumeCustomerNum = res.data.data.consumeCustomerNum\r\n                    console.log(this.consumeCustomerNum)\r\n                    this.customerBalanceNum = res.data.data.customerBalanceNum\r\n                    this.customerNum = res.data.data.customerNum\r\n                    this.sumBalance = res.data.data.sumBalance\r\n                    this.sumCharge = res.data.data.sumCharge\r\n\r\n                })\r\n            },\r\n            order(){\r\n                //预约\r\n                this.$axios({\r\n\t\t\t\t\turl: this.$BASE_ORDER_URL +  \"schedule/queryScheduleSummary\",\r\n                    method: \"get\",\r\n                    params:{\r\n                        departmentId:null,\r\n                        startTime:this.time1==\"\"?null:this.time1,\r\n                        endTime:this.time2==\"\"?null:this.time2,\r\n                    }\r\n\t\t\t\t}).then(res => {\r\n                    console.log(res)\r\n                    this.Order = res.data.data\r\n\r\n                })\r\n                this.show = false;\r\n                //订单\r\n                   this.$axios({\r\n                    url: this.$BASE_ORDER_URL + \"ordermanage/queryOrderSummary\",\r\n                    method:\"get\",\r\n                    params:{\r\n                        departmentId:null,\r\n                        startTime:this.time1==\"\"?null:this.time1,\r\n                        endTime:this.time2==\"\"?null:this.time2,\r\n                    }\r\n                    }).then(res => {\r\n                            console.log(res)\r\n                            this.sum_number = res.data.data.sumNumber\r\n                            this.totel_price = res.data.data.totelPrice\r\n                        })\r\n                //会员\r\n                 this.$axios({\r\n                    url: this.$BASE_USER_URL + \"gogirl_user/countCustomerInfo\",\r\n                    method:\"get\",\r\n                    params:{\r\n                        departmentId:null,\r\n                        startTime:this.time1==\"\"?null:this.time1,\r\n                        endTime:this.time2==\"\"?null:this.time2,\r\n                    }\r\n                    }).then(res => {\r\n                        console.log(res)\r\n                        this.consumeCustomerNum = res.data.data.consumeCustomerNum\r\n                        this.customerBalanceNum = res.data.data.customerBalanceNum\r\n                        this.customerNum = res.data.data.customerNum\r\n                        this.sumBalance = res.data.data.sumBalance\r\n                        this.sumCharge = res.data.data.sumCharge\r\n\r\n                    })\r\n            },\r\n            in1(){\r\n                this.show2 = true;\r\n            },\r\n            in2(){\r\n                this.show3 = true;\r\n            },\r\n            Finish3(){\r\n                    this.show3 = false;\r\n            },\r\n            Finish2(){\r\n                    this.show2 = false;\r\n\t\t\t},\r\n            Finish1(){\r\n\t\t\t\t\tlet year = this.currentDate.getFullYear();\r\n                    let month = this.currentDate.getMonth() + 1;\r\n                    if(month<10){\r\n                        month = \"0\" + month;\r\n                    }\r\n                    let day = this.currentDate.getDate();\r\n                    if(day<10){\r\n                        day = \"0\" + day;\r\n                    }\r\n                    let time = year + \"-\" + month + \"-\" + day\r\n                    this.time2 = time\r\n                    console.log(this.time2)\r\n                    this.show3 = false;\r\n\t\t\t},\r\n            Finish(){\r\n\t\t\t\t\tlet year = this.currentDate.getFullYear();\r\n                    let month = this.currentDate.getMonth() + 1;\r\n                    if(month<10){\r\n                        month = \"0\" + month;\r\n                    }\r\n                    let day = this.currentDate.getDate();\r\n                    if(day<10){\r\n                        day = \"0\" + day;\r\n                    }\r\n                    let time = year + \"-\" + month + \"-\" + day\r\n                    this.time1 = time\r\n                    this.show2 = false;\r\n            },\r\n            //隐藏元获取到的元素$refs\r\n            AllTime(){\r\n                this.$refs.D1.style.display = 'none'\r\n                this.$refs.D2.style.display = 'block'\r\n                this.show = true\r\n\r\n            },\r\n            DisplayStore(){\r\n                this.show1 = true;\r\n                this.$refs.D2.style.display = 'none'\r\n                this.$refs.D1.style.display = 'block'\r\n            },\r\n            off0(){\r\n                this.store1 = this.store1\r\n                this.show1 = false;\r\n                this.$axios({\r\n                    url: this.$BASE_ORDER_URL + \"ordermanage/queryOrderSummary\",\r\n                    method:\"get\",\r\n                    params:{\r\n                        departmentId:null,\r\n                        startTime:this.time1==\"\"?null:this.time1,\r\n                        endTime:this.time2==\"\"?null:this.time2,\r\n                    }\r\n                    }).then(res => {\r\n                            console.log(res)\r\n                            this.sum_number = res.data.data.sumNumber\r\n                            this.totel_price = res.data.data.totelPrice\r\n                        })\r\n                //预约\r\n                this.$axios({\r\n\t\t\t\t\turl: this.$BASE_ORDER_URL +  \"schedule/queryScheduleSummary\",\r\n                    method: \"get\",\r\n                    params:{\r\n                        departmentId:null,\r\n                        startTime:this.time1==\"\"?null:this.time1,\r\n                        endTime:this.time2==\"\"?null:this.time2,\r\n                    }\r\n\t\t\t\t}).then(res => {\r\n                    console.log(res)\r\n                    this.Order = res.data.data\r\n                })\r\n                //会员\r\n                this.$axios({\r\n\t\t\t\t\turl:  this.$BASE_USER_URL +  \"gogirl_user/countCustomerInfo\",\r\n                    method: \"get\",\r\n                    params:{\r\n                        departmentId:null,\r\n                        startTime:this.time1==\"\"?null:this.time1,\r\n                        endTime:this.time2==\"\"?null:this.time2,\r\n                    }\r\n\t\t\t\t}).then(res => {\r\n                    console.log(res)\r\n                    this.consumeCustomerNum = res.data.data.consumeCustomerNum\r\n                    console.log(this.consumeCustomerNum)\r\n                    this.customerBalanceNum = res.data.data.customerBalanceNum\r\n                    this.customerNum = res.data.data.customerNum\r\n                    this.sumBalance = res.data.data.sumBalance\r\n                    this.sumCharge = res.data.data.sumCharge\r\n\r\n                })\r\n            },\r\n            storeChange(id,name){\r\n                if(id&&id!=0){\r\n                    this.id= id\r\n                    this.show1 = false;\r\n                }else{\r\n                    this.id= null\r\n                    this.show1 = false;\r\n                }\r\n                this.store1 = name\r\n                this.queryInfo();\r\n            },\r\n            timeChange1(){//所有\r\n                this.show = false;\r\n                this.time1 = null;\r\n                this.time2 = null;\r\n                this.store2 = \"所有时间\"\r\n                this.queryInfo();\r\n            },\r\n            timeChange2(){//当天\r\n                this.show = false;\r\n                let year = this.currentDate.getFullYear();\r\n                let month = this.currentDate.getMonth() + 1;\r\n                    if(month<10){\r\n                        month = \"0\" + month;\r\n                    }\r\n                let day = this.currentDate.getDate();\r\n                    if(day<10){\r\n                        day = \"0\" + day;\r\n                    }\r\n                let time = year + \"-\" + month + \"-\" + day\r\n                this.time1 = time//今天日期\r\n                this.time2 = time//今天日期\r\n                this.store2 = this.time1+'~'+this.time2\r\n                this.queryInfo();\r\n            },\r\n            timeChange3(){\r\n                this.show = false;\r\n                let year = this.currentDate.getFullYear();\r